import api from './api'

import {
  commonParams
} from './config'
// /

export function loginByVcode(obj){
  const url = '/login/byVcode.shtml'
  const data = Object.assign({}, commonParams,obj)
  return api.post(url,data).then((res)=>{
    return Promise.resolve(res.data)
  })
}



