import axios from 'axios'
import qs from 'qs'
import NProgress from 'nprogress'
import {ERR_OK} from './config'
import { Message } from 'element-ui'
import {Storage,Cookie} from '../common/js/common'
/**/
axios.interceptors.request.use(config =>{
	//
	NProgress.start()
	/*if (Storage.get('token')) {  // tokenhttp headertoken
	     config.headers.Authorization = Storage.get('token');
	}*/
	if(Cookie.get('token')) {  // tokenhttp headertoken
	    config.headers.Authorization = Cookie.get('token');
	}
	return config
},error=>{
	return Promise.reject(error)
})



/**/
axios.interceptors.response.use(response=>{
	return response
},error=>{
	//catch
	return Promise.resolve(error.response)
})

/*
 return response 
 {
  // 
  data: {},
  // HTTP
  status: 200,
  // HTTP
  statusText: 'OK',
  // 
  headers: {},
  // axios 
  config: {}
}
 * */

/*getpost*/
export default{
	post(urlstr,data){
		return axios({
			method:'post',
			url:urlstr,
			data:qs.stringify(data),  //post
			timeout:30000,//
			headers:{
				'X-Requested-With':'XMLHttpRequest',
                'Content-Type':'application/x-www-form-urlencoded;charset=UTF-8'
			}
		}).then(checkStatus).then(checkCode)
	},
	get(urlstr,params){
		return axios({
			method:'get',
			url:urlstr,
			params,
			timeout:30000,
			headers:{
				'X-Requested-With': 'XMLHttpRequest'
			}
		}).then(checkStatus).then(checkCode)
	}
}
/* data qs.stringify(data), headersContent-Type, ,  $_POST 
, */

function checkStatus(response){
	//
	NProgress.done()
	//http
	if(response && response.status === 200 || response.status === 304){
		return response
		//data return response.data 
	}
	//
	//404500
	return{
		data:{
			coede:-404,
			message:response.statusText,
			data:response.statusText
		}
	}
	 //  return  response.data, 
    // return {
    //    code: -404,
    //    message: response.statusText,
    //    data: response.statusText,
    //}
}

// checkCode
function checkCode(res) {
    //  code (, , ), , 
    if(res.data.code){
    	 if (res.data.code !== ERR_OK && res.config.data.indexOf('diffCode') === -1) {// res.code, 
	       if(res.data.code == 'W00020'){
		    	/*Storage.set('token','')*/
		    	Cookie.dele('token')
		    	//this.$router.push({name:'login'});
		    	location.href = '#/login'
		    }else{
		    	 Message({
			       	message:res.data.msg,
			       	type:'error'
			       })
		    }
	    }
    }
    return res
}





