export function userRegist(obj){
  const url = '/regist/regAccount.shtml'
  const data = Object.assign({'vcodeKey':randomNum(1000,9999)}, commonParams,obj)
  return api.post(url,data).then((res)=>{
    return Promise.resolve(res.data)
  })
}

