import api from './api'

import {
  commonParams
} from './config'

export function bindMobileForjk (obj){
  //obj 
  const url = '/wx/bindMobileForjk.shtml'
  // url
  const data = Object.assign({},commonParams,obj)
  //  Object.assign() 
  return api.post(url,data).then((res)=>{
    return Promise.resolve(res.data)
  })
}



