<template>
  <div>
    <div class="item">
      <input
        :class="[{error:errorMsg}]"
        type="password"
        v-model="pwd"
        placeholder="(8-20)"
        @blur = "checkValue($event.target.value)"
      >
      <div :class="['error-msg',{error:errorMsg}]">{{errorMsg}}</div>
    </div>
    <div class="item">
      <input
        :class="[{error:errorMsgAngin}]"
        type="password"
        :value="value"
        placeholder=""
        @blur = "chechValueAngin($event.target.value)"
        @input="$emit('input', $event.target.value)"
      >
      <div :class="['error-msg',{error:errorMsgAngin}]">{{errorMsgAngin}}</div>
    </div>
  </div>

</template>

<script>
  export default {
    name: 'phone',
    data() {
      return {
        pwd: '',
        errorMsg:'',
        errorMsgAngin: ''
      }
    },
    methods: {
      checkValue (val) {
        val = val || this.pwd;
        if (val.length < 8 || val.length > 20) {
          this.errorMsg ="(8-20)";
          return ;
        }
        if (!/\D+/g.test(val)) {
          this.errorMsg =""
          return ;
        }
        if (!/[^\a-\z\A-\Z]/g.test(val)) {
          this.errorMsg =""
          return ;
        }
        if (/\s/g.test(val)) {
          this.errorMsg =""
          return ;
        }
        this.errorMsg =  "";
      },
      chechValueAngin (val) {
        if (this.pwd !== val) {
          this.errorMsgAngin =  "";
        }else{
          this.errorMsgAngin = "";
        }
        return !this.errorMsg
      }
    },
    props:['value','isRight']
  }
</script>
<style scoped lang="less">
  @import "../../assets/css/input";
  .forget-password {
    position: absolute;
    top: 19px;
    left: 80%;
    font-size: 13px;
    cursor: pointer;
  }
</style>



